// ==== LAYOUT ==== //

// These wrappers allow for backgrounds and layout options to be defined for each of the four sections (header, content, sidebar, and footer)
// There is also a fifth meta-wrapper to bind content and sidebar on large screens

// A general wrapper for various site elements

.inner-container {
  max-width: 1200px;
  padding: 30px;
  margin: 0 auto;
}
%wrap {
  box-sizing: border-box; // Makes life a little easier
  padding: 0;
}
.wrap-header {
  z-index: 99;
  height: 100px;
  @extend %wrap;
  background: color(header-background);
  @include media-above($break-large) {
    padding-left: 0;
    padding-right: 0;
  }
}
.container { // Wrapper around content and sidebar
  @extend %clearfix;
  @include media-above($break-large) {
    @include scut-center-block($width-site);
    padding: spacer(1);
  }
}
.wrap-content {
  @extend %wrap;
  padding: spacer();
  @include media-above($break-large) {
    float: left;
    padding-left: 0;
    padding-right: spacer();
    width: 100%;
  }
}
.wrap-footer {
  @extend %wrap;
    background: color(footer-background);
  padding-bottom: spacer(1);
  padding-top: spacer(1);
  @include media-above($break-large) {
    padding-left: spacer(2);
    padding-right: spacer(2);
  }
  @include media-below($break-medium) {
    padding-bottom: spacer(1);
  }
}
.site-header,
.site-footer {
  // @include scut-center-block($width-site);
}
.site-title {
  margin: 0;
}
.site-description {
  @extend %hide;
}
.site-content {
  @extend %spacer-below;
  article {
    @extend %spacer-below-2;
  }
}
.site-navigation {
  display: none;
  @include media-above($break-medium) {
    display: block !important; // Handles an edge case where user resizes after menu is hidden on smaller screens
  }
  &.active {
    display: block;
  }
}
.alignright {
  float: right;
}
.alignleft {
  float: left;
}


.grey-bg {
  background: $grey-light;
}

.fullW {
  width: 100vw;
  display: inline-block;
  position: relative;
}

.site-main{
  padding-top: 80px;
}

.jobs-container {
  padding-top: 0;
}

.page-id-46 {
  .inner-container {
    padding-bottom: 0;
  }
}

.ja-job-search {
@extend .grey-bg;
padding: 30px;
}

.ja-field-container {
  width: 20%;
  display: inline-block;
  float: left;
  label {
    display: none;
  }
  label[for="ja-keywords"] {
    display: block;
    margin-bottom: 5px;
  }

  select {
    background: white;
    border: 1px solid black;
    padding: 20px 10px;
    width: 90%;
    font-family: $opensans;
    height: 40px;
    margin-top: 20px;
  }

}

.ja-submit {
  width: 100%;
  display: inline-block;
  right: 0;
  cursor: pointer;
  .ja-button {
    cursor: pointer;
      border: none;
      text-align: center;
      width: 260px;
      padding: 15px 40px;
      margin: 20px auto 0 auto;
      display: block;
  }

}


.ja-job-list-container, .ja-job-details {
  width: 60%;
  margin-left: 20%;
  margin-top: 80px;

  .job {
    margin-bottom: 60px;
  }
}

.classifications {
  list-style-type: none;
  padding: 0;
  margin: 0;
  li {
    display: inline-block;
    border: 1px solid $grey-light;
    border-radius: 20px;
    background: $grey-light;
    padding: 8px 20px;
    margin: 0 10px 10px 0;
    font-size: 13px;
    // color: white;

  }
}

.powered-by-jobadder {
  display: none;
}

.title {
  a {
    color: $grey;
  }
}

.ja-pager-summary {
  display: none;
}

.ja-pager {
      margin-left: -40px;
  span , a {
    background: $primary-org;
    color: white;
    padding: 8px 15px;
    border-radius: 8px;
    margin-right: 8px;
  }

  a {
    background: rgba(240,114,70,0.5)
  }
}


.site-footer {
  @media screen and (max-width: 48em) {
  padding: 20px;
}
}

.apply {
  margin: 20px 0;
  
  .ja-button {
    margin: 0;
    padding: 12px 25px;
    height: 45px
    
  }
}
.back-link {
    display: inline-block;
      height: 45px;
    font-weight: 600;
    border: 2px solid $primary-org;
    padding: 12px 25px;
    letter-spacing: 1.3px;
    text-align: center;
    border-radius: 8px;
    -webkit-transition: all .5s ease;
    transition: all .5s ease;
    color: $primary-org;
    text-transform: uppercase;
    font-size: 13px;
}

.slide-wrap {
  padding: 20px;
}


article.post {
  margin: 0 0 50px 0;
}
.post-title {
  @extend .monst-light;
  margin: 1rem 0;
  
  a {
    color: $grey;
  }
}



.pagination {
  span , a {
    background: $primary-org;
    color: white;
    padding: 8px 15px;
    border-radius: 8px;
    margin-right: 8px;
  }

  a, .dots {
    background: rgba(240,114,70,0.5);
    
    &:hover {
      background: rgba(240,114,70,0.8)
    }
  }
}

.cat-list, .arch-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  li a{
    color: $grey;
  }
  
  .current-cat a {
      color: $primary-org;
  }
}

.footer-post-wrap {
  margin: 0 0 20px 0;
  .date {
    font-style: italic;
    display: block;
    font-size: .7rem;
    @extend .monst-light;
  }
}

.site-footer {
.footer-news {
  text-transform: uppercase;
  font-size: .8em;
}
}

.footer-logo-wrap {
  position: relative;
  img {
    position: absolute;
    bottom: 30px;
    left: 0;
  }
}

  @media screen and (max-width: 48em) {
    .footer-logo-wrap {
      img {
        bottom: 0;
        left: 15px;
      }
  }
}


.professions {
